html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}

html {
  font-size: 14px;
}

body {
  margin: 0;
  font-size: 14px;
  background-color: #fff;
  font-family: Arial, sans-serif;
}

.wrapper {
  min-height: 100%;
  height: auto;
  overflow: hidden;
  position: relative;
}

.container {
  display: flex;
  width: 1240px;
  margin: 0 auto;
  position: relative;
}

.hero {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}

* {
  box-sizing: border-box;
}

.header {
  height: 127px;
  padding-top: 28px;
}

.header__list {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.header__item-up {
  margin-bottom: 15px;
}

.header__tagline {
  font-family: 'SegoeUIBlack' Arial sans-serif;
  font-weight: 800;
  color: #393185;
}

.header__text {
  font-family: 'SegoeUI' Arial sans-serif;
  font-size: 16px;
  color: #262626;
}

.header__text--link {
  color: #2e2771;
  text-decoration: none;
  border-bottom: 1px solid #a8a5c9;
}

.header__text--bold {
  font-family: 'SegoeUIBlack' Arial sans-serif;
  font-weight: bold;
}

.header__order {
  display: block;
  font-family: 'SegoeUI' Arial sans-serif;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #e50a62;
  color: #fff;
  width: 207px;
  height: 40px;
  padding-top: 15px;
}

.header__order:hover {
  border: 1px solid #e50a62;
  background-color: #fff;
  color: #e50a62;
}

.about {
  height: 35px;
  padding-top: 10px;
  border-bottom: 1px solid #e2e2ed;
}

.about__content {
  display: flex;
  justify-content: space-between;
}

.about__list {
  display: flex;
}

.about__item {
  margin-right: 58px;
}

.about__link,
.about_add {
  display: block;
  font-family: 'SegoeUI' Arial sans-serif;
  font-size: 11px;
  color: #2e2771;
  text-transform: uppercase;
  text-decoration: none;
}

.menu {
  width: 100%;
  height: 75px;
  background-color: #393185;
  padding-top: 19px;
}

.menu__list {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.menu__link {
  position: relative;
  color: #fff;
  font-family: 'FuturaFuturisCondBoldC' Arial sans-serif;
  font-size: 24px;
  text-transform: uppercase;
  text-decoration: none;
}

.menu__link:after {
  content: "";
  display: block;
  background-color: #fff;
  position: absolute;
  right: -24px;
  top: 12px;
  width: 6px;
  height: 2px;
  transform: rotate(45deg);
}

.menu__link:before {
  content: "";
  display: block;
  position: absolute;
  right: -27px;
  top: 12px;
  width: 6px;
  height: 2px;
  transform: rotate(-45deg);
  background-color: #fff;
}

.content {
  width: 100%;
  height: calc(100% - 237px);
  background-image: linear-gradient(to top, rgba(41, 36, 58, 0.9), rgba(57, 49, 133, 0.9));
  background-size: cover;
  position: relative;
}

.content:before {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: url("../img/block/content/bg.png") center center no-repeat;
  background-size: cover;
}

.content__block {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}

.content__caption-text {
  font-family: 'FuturaFuturisCondBoldC' Arial sans-serif;
  font-size: 108px;
  color: #fff;
  text-transform: uppercase;
}

.content__list {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.slide__item {
  background-color: #393185;
  width: 187px;
  height: 52px;
  padding-top: 15px;
  padding-left: 30px;
  border-bottom: 1px solid #473d94;
}

.slide__item:first-child {
  background-color: #e50a62;
}

.slide__link {
  display: block;
  font-family: 'SegoeUI' Arial sans-serif;
  font-size: 14px;
  font-weight: bolder;
  color: #fff;
  text-decoration: none;
}

.content__item-text {
  font-family: 'SegoeUIBlack' Arial sans-serif;
  font-size: 30px;
  font-weight: bolder;
  color: #fff;
}

.content__item-text,
.order {
  width: 295px;
}

.kind__item {
  position: relative;
  padding-left: 40px;
  margin-bottom: 25px;
}

.kind__item:after {
  content: "";
  display: block;
  background-color: #fff;
  position: absolute;
  left: 0px;
  top: 13px;
  width: 7px;
  height: 3px;
  transform: rotate(45deg);
}

.kind__item:before {
  content: "";
  display: block;
  position: absolute;
  left: 3px;
  top: 12px;
  width: 12px;
  height: 3px;
  transform: rotate(-45deg);
  background-color: #fff;
}

.kind__link {
  display: block;
  font-family: 'SegoeUI' Arial sans-serif;
  font-size: 24px;
  color: #fff;
  text-decoration: none;
}

.order__phone {
  font-family: 'SegoeUIBlack' Arial sans-serif;
  font-size: 30px;
  font-weight: bolder;
  color: #fff;
  margin-bottom: 25px;
}

.order__link {
  display: block;
  font-family: 'SegoeUI' Arial sans-serif;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #e50a62;
  color: #fff;
  width: 100%;
  height: 40px;
  padding-top: 15px;
}